import React from "react";
import axios from 'axios'

const baseUrl = '/api/recipes'

export default class AddRecipe extends React.Component {
  state = {
    title: "",
    ingredients: [""],
    description: "",
    type: "",
  };

  submitRecipe = (e) => {
    e.preventDefault();
    if (this.title !== "") {
        const recipeObject = {
            title: this.state.title,
            ingredients: this.state.ingredients,
            description: this.state.description,
            type: this.state.type
        }
        axios.post(baseUrl, recipeObject).then(response => console.log(response.data))
        this.setState({
          title: "",
          ingredients: [""],
          description: "",
          type: "",
        });
    }
  };

  addIngredient = (e) => {
    e.preventDefault();
    this.setState({
      ingredients: [...this.state.ingredients, ""],
    });
  };
  handleIngredientChange = (e, index) => {
    const ingredientList = [...this.state.ingredients];
    ingredientList[index] = e.currentTarget.value;
    this.setState({
      ingredients: ingredientList,
    });
  };
  titleChange = (e) => {
    this.setState({
      title: e.currentTarget.value,
    });
  };
  descriptionChange = (e) => {
    this.setState({
      description: e.currentTarget.value,
    });
  };
  typeChange = (e) => {
    this.setState({
      type: e.currentTarget.value,
    });
  };

  render() {
    return (
      <form className="form form--new-recipe">
        <input
          type="text"
          className="form__title"
          onChange={this.titleChange}
          value={this.state.title}
        ></input>
        {this.state.ingredients.map((ingredient, i) => (
          <input
            type="text"
            className="form__ingredient"
            onChange={e => this.handleIngredientChange(e, i)}
            value={ingredient}
          />
        ))}
        <button class="add_form_field" onClick={this.addIngredient}>
          Add New Field &nbsp; <span>+ </span>{" "}
        </button>
        <input
          type="text"
          className="form__description"
          onChange={this.descriptionChange}
          value={this.state.description}
        ></input>
        <input
          type="text"
          className="form__type"
          onChange={this.typeChange}
          value={this.state.type}
        ></input>
        <input
          type="submit"
          value="Add Recipe"
          onClick={this.submitRecipe}
        ></input>
      </form>
    );
  }
}
